* {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: darkgray;
  font-size: 1.2rem;
  width: 100%;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  box-shadow: 2px 2px 0 0 darkgray;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  color: darkgray;
  font-size: small;
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 100px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}